<template>
<header id="header">
	<div class="poligon">
		<div class="logo">
			<!--<span class="icon fa-gem"></span>-->
		</div>
	</div>
	<div class="content">
		<div class="particles_switch">
			<span class="particles_switch_text">{{switch_text}} Animation</span>
			<input type="checkbox"  class="toogle_particles_checkbox" id="toggle" @change="toogle_particles($event)"/>
			<label class="particles_label" for="toggle"></label>
		</div>
		<div class="inner">
			<h1>{{ person_name }}</h1>
			<p>Postdoctoral Fellow at AVLabs, Khalifa University Verified email at ku.ac.ae - <a href="https://www.linkedin.com/in/arnukk/"> Homepage </a></p>
		</div>
	</div>
	<nav>
		<ul>
			<li><a href="#intro">Intro</a></li>
			<li><a href="#work">Work</a></li>
			<li><a href="#about">About</a></li>
			<li><a href="#contact">Contact</a></li>
			<!--<li><a href="#elements">Elements</a></li>-->
		</ul>
	</nav>
</header>
</template>

<script>
import '@/assets/css/main.css';
export default {
	name: 'Header',
	props: {
		person_name: String
	},
	methods: {
		toogle_particles: function(e) {
			if(e.target.checked){
				this.particlePlay();
				this.switchText('Disable');
				if(document.getElementById("particles-js").style.display=='none'){
					document.getElementById("particles-js").style.display='block';
				}
			}else{
				this.particlePause();
				this.switchText('Enable');
			}
		},
		particlePause:function(){
			window._tempFn = window.pJSDom[0].pJS.fn.vendors.draw;
			window.pJSDom[0].pJS.fn.vendors.draw = () => {};
		},
		particlePlay:function(){
			window.pJSDom[0].pJS.fn.vendors.draw = window._tempFn;
			window.pJSDom[0].pJS.fn.vendors.draw();
		},
		switchText:function(text){
			this.switch_text=text;
		},
	},
	data() {
        return {
            switch_text: 'Enable'
        }
    },
	mounted () {
		this.particlePause();
		this.particlePlay();
		
		var mouseDown = 0;
		document.body.onmousedown = function() { 
			mouseDown = 1;
		}
		document.body.onmouseup = function() {
			mouseDown = 0;
		}
		
		
		//let slider = document.querySelector('.wrapper'),
		let slider = document.querySelector('#wrapper'),
		sliderTrack = slider.querySelector('.slider-track'),
		slides = slider.querySelectorAll('.slide'),
		//arrows = slider.querySelector('.slider-arrows'),
		//prev = arrows.children[0],
		//next = arrows.children[1],
		//slideWidth = slides[0].offsetWidth,
		slideIndex = 1,
		posInit = 0,
		posX1 = 0,
		posX2 = 0,
		posFinal = 0,
		posThreshold = slides[0].offsetWidth * 0.35,
		trfRegExp = /([-0-9.]+(?=px))/,
		getEvent = function() {
		return (event.type.search('touch') !== -1) ? event.touches[0] : event;
		},
		
		slide = function() {
		//if (transition) {
		//	sliderTrack.style.transition = 'transform .5s';
		//}
		//sliderTrack.style.transform = `translate3d(-${slideIndex * slideWidth}px, 0px, 0px)`;

		//prev.classList.toggle('disabled', slideIndex === 0);
		//next.classList.toggle('disabled', slideIndex === --slides.length);
		},
		swipeStart = function() {
		let evt = getEvent();

		posInit = posX1 = evt.clientX;

		sliderTrack.style.transition = '';

		document.addEventListener('touchmove', swipeAction);
		document.addEventListener('mousemove', swipeAction);
		document.addEventListener('touchend', swipeEnd);
		document.addEventListener('mouseup', swipeEnd);
		},
		swipeAction = function() {

			let evt = getEvent(),
			style = sliderTrack.style.transform,
			transform = +style.match(trfRegExp)[0];
			console.log(evt);
			console.log(mouseDown);

			posX2 = posX1 - evt.clientX;
			posX1 = evt.clientX;
			console.log((transform - posX2));
			console.log(posThreshold);
			if((transform - posX2)>posThreshold)
				sliderTrack.style.transform = `translate3d(${transform - posX2}px, 0px, 0px)`;
		},
		swipeEnd = function() {
		posFinal = posInit - posX1;

		document.removeEventListener('touchmove', swipeAction);
		document.removeEventListener('mousemove', swipeAction);
		document.removeEventListener('touchend', swipeEnd);
		document.removeEventListener('mouseup', swipeEnd);
		var slides =  new Object();
			slides[1]='#intro';
			slides[2]='#work';
			slides[3]='#about';
			slides[4]='#contact';
		var hashes  =  new Object();
			hashes['#intro']=1;
			hashes['#work']=2;
			hashes['#about']=3;
			hashes['#contact']=4;
			if (Math.abs(posFinal) > posThreshold) {
				let hash=document.querySelectorAll('nav a.active')[0]['hash'];
				slideIndex=hashes[hash];
				if (posInit < posX1) {
					if(slideIndex>1){
						slideIndex--;
					}else{
						slideIndex=4;
					}
					document.querySelector(slides[slideIndex]+'_icon').click();
					sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';
				} else if (posInit > posX1) {
					if(slideIndex<4){
						slideIndex++;
					}else{
						slideIndex=1;
					}
					document.querySelector(slides[slideIndex]+'_icon').click();
					sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';
				}
			}

			if (posInit !== posX1) {
				slide();
			}
		};

		sliderTrack.style.transform = 'translate3d(0px, 0px, 0px)';

		slider.addEventListener('touchstart', swipeStart);
		slider.addEventListener('mousedown', swipeStart);


		
	}
}

</script>

